.slideContainer {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;

    background: linear-gradient(0deg, rgb(0, 0, 0) 20%, rgb(86, 8, 68) 75%);

    
}

.slideGrid {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 1fr min-content 1fr;
    justify-items: center;

}

.slide {
    grid-row: 2/3;
}

.slideNav {
    /* position: relative; */
    text-align: center;
}

.slideNav > div {
    padding-left: 1em;
    padding-right: 1em;
}

.text {
    user-select: none;
    text-align: center;
    font-size: 250%;
    color: white;
}

.letter, .letterContainer {
    display: inline-block;
    padding: 0.1em;
}

.space {
    padding-right: 0.5em;
}